<template>
  <div>
    <el-card class="welcome-card note mx-3">
      <div class="clearfix" slot="header">
        <span>30</span>
      </div>

      <el-col :span="24" class="echarts-item">
        <ve-line :data="chartData" :extend="extend" :settings="chartSettings"></ve-line>
      </el-col>
    </el-card>
    <el-card class="welcome-card m-3">
      <div class="clearfix" slot="header">
        <span></span>
      </div>
      <el-table :data="logsData" border highlight-current-row style="width: 100%;font-size: 12px;" v-loading="isLogLoading">
        <el-table-column align="center" label="" prop="operater" width="100px"></el-table-column>
        <el-table-column align="center" label="" prop="ipAddress" width="100px"></el-table-column>
        <el-table-column align="center" label="" prop="operateTime" width="135px"></el-table-column>
        <el-table-column header-align="center" label="" prop="url" width="250"></el-table-column>
        <el-table-column align="center" label="" prop="httpMethod" width="65"></el-table-column>
        <el-table-column align="center" label="()" prop="elapsedTime" width="65"></el-table-column>
        <el-table-column header-align="center" label="" prop="param" show-overflow-tooltip></el-table-column>
        <el-table-column header-align="center" label="Agent" prop="agent" show-overflow-tooltip width="180"></el-table-column>
        <el-table-column align="center" header-align="center" label="" prop="isEnabled" width="90">
          <template slot-scope="scope">
            <el-tag :type="scope.row.success?'success':'danger'" disable-transitions>{{scope.row.success ? "":""}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
      <div class="d-flex justify-content-end align-items-center">
        <el-pagination
          :current-page.sync="logQueryParam.PageIndex"
          :page-size="logQueryParam.PageSize"
          :total="logsTotalCount"
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
          background
          layout="prev, pager, next"
        ></el-pagination>
      </div>
      <br />
    </el-card>

    <el-card class="mx-3">
      <div class="clearfix" slot="header">
        <span></span>
      </div>
      <el-aside>1vue</el-aside>
      <br />
      <hr />
      <br />
      <el-aside>2</el-aside>
      <br />
      <div style="height: 300px;overflow-y: auto;">
        <el-steps direction="vertical">
          <el-step description=" TestControllerBaseAdminController" title=" 1"></el-step>
          <el-step description=" /api/Test/get " title=" 2"></el-step>
          <el-step description="vueadmin  api" title=" 3.1"></el-step>
          <el-step description="" title=" 3.2"></el-step>
          <el-step description="" title=" 3.3"></el-step>
          <el-step description="netcore" title=" 4"></el-step>
          <el-step description="Admin" title=" 5"></el-step>
        </el-steps>
      </div>
    </el-card>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import homeApi from '../api/admin/home'
import * as signalR from '@aspnet/signalr'
export default {

  name: 'Welcome',
  components: { VeLine },
  data() {
    return {
      baseUrl: process.env.VUE_APP_BASE_API,
      listLoading: false,
      welcomeInitData: {},
      logsData: [],
      isLogLoading: false,
      logsTotalCount: 0,
      logQueryParam: {
        pageIndex: 1,
        pageSize: 5,
        sortKey: '',
        sortType: 0,
        isWelCome: true
      },
      serverInfo: {},
      extend: {
        series: {
          label: {
            normal: {
              show: true
            }
          }
        }
      },
      chartData: {
        columns: [],
        rows: [],
        today: 0
      },
      // chartData: {
      //   columns: ['', '', '', ''],
      //   rows: [
      //     { : '1/1', : 1393, : 1093, : 0.32 },
      //     { : '1/2', : 3530, : 3230, : 0.26 },
      //     { : '1/3', : 2923, : 2623, : 0.76 },
      //     { : '1/4', : 1723, : 1423, : 0.49 },
      //     { : '1/5', : 3792, : 3492, : 0.323 },
      //     { : '1/6', : 4593, : 4293, : 0.78 }
      //   ]
      // },
      chartSettings: {
        metrics: ['count'],
        dimension: ['name'],
        legendName: { 'count': '' }
      },
      connection: ''
    }
  },
  methods: {
    getBck(index) {
      return `background: rgb(${43 + index * 20}, ${148 +
        index * 10}, 255) none repeat scroll 0% 0%;`
    },
    handleCurrentChange(val) {
      this.logQueryParam.PageIndex = val
      this.getLogsList()
    },
    handleSizeChange(val) {
      this.logQueryParam.PageSize = val
      this.getLogsList()
    },
    getLogsList() {
      var that = this
      homeApi.getLogsPage(that.logQueryParam)
        .then((res) => {
          that.logsTotalCount = res.data.totalCount
          that.logsData = res.data.items
        })
    },
    getLineData() {
      homeApi.getLineData()
        .then(res => {
          this.chartData = res.data
        })
    },
    buildConnection() {
      var that = this
      // 1
      this.connection = new signalR.HubConnectionBuilder()
        // 
        .withUrl(`${that.baseUrl}/chathub`, {
          accessTokenFactory: () => that.$store.getters.auth.access_token
        })
        // 
        .configureLogging(signalR.LogLevel.Information)
        // 
        .build()

      // 
      that.connection.start().then(() => {
        that.connection.invoke('ReceiveUpdate', '2222').catch(function (err) {
          return console.error(err)
        })

        // that.connection.invoke('SendMessage', this.$store.getters.user.userId, '').catch(function (err) {
        //   return console.error(err)
        // })
        console.log('')
      })

      that.connection.on('ReceiveUpdate', function (update) {
        console.info('update ReceiveUpdate success!', update)
        // that.tableData = update;// data 
      })

      that.connection.on('ReceiveMessage', function (update, message) {
        console.info('update ReceiveMessage success!', update, message)
        // that.tableData = update;// data 
      })
    }
  },
  created() {
    this.getLogsList()
    this.getLineData()
    this.buildConnection()
  }
}
</script>

<style scoped>
.bg-blue-sub-item {
  max-width: 120px !important;
  height: 50px;
  float: left;
  color: #fff;
  font-size: 15px;
  line-height: 50px;
  padding: 0 10px;
}
.note .text {
  font-size: 14px;
}

.note .item {
  margin-bottom: 18px;
}
</style>

<style scoped>
.panel-group {
  margin-top: 18px;
}
.card-panel-col {
  margin-bottom: 32px;
  width: 115px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  margin-right: 2%;
  float: right;
}

.card-panel {
  height: 108px;
  font-size: 12px;
  position: relative;
  overflow: hidden;
  color: #666;
  background: #fff;
  box-shadow: 4px 4px 40px rgba(0, 0, 0, 0.05);
  border-color: rgba(0, 0, 0, 0.05);
}

.card-panel .card-panel-icon-wrapper {
  color: #fff;
}

.card-panel .card-panel .icon-people {
  background: #40c9c6;
}

.card-panel .card-panel .icon-message {
  background: #36a3f7;
}

.card-panel .card-panel .icon-money {
  background: #f4516c;
}

.card-panel .card-panel .icon-shopping {
  background: #34bfa3;
}

.card-panel .icon-people {
  color: #40c9c6;
}

.card-panel .icon-message {
  color: #36a3f7;
}

.card-panel .icon-money {
  color: #f4516c;
}

.card-panel .icon-shopping {
  color: #34bfa3;
}

.card-panel .card-panel-icon-wrapper {
  float: left;
  margin: 14px 0 0 14px;
  padding: 16px;
  transition: all 0.38s ease-out;
  border-radius: 6px;
}

.card-panel .card-panel-icon {
  float: left;
  font-size: 48px;
}

.card-panel .card-panel-description {
  float: left;
  font-weight: bold;
  margin-left: 30px;
  margin-top: 20px;
}
.card-panel .card-panel-description .card-panel-text {
  line-height: 18px;
  color: rgba(0, 0, 0, 0.45);
  font-size: 16px;
  margin-bottom: 12px;
}

.card-panel .card-panel-description .card-panel-num {
  font-size: 36px;
  color: #f4516c;
}
.extoday {
  cursor: pointer;
}
.card-acuser-num {
  font-size: 36px;
  color: #40c9c6;
}

@media (max-width: 550px) {
  .card-panel-description {
    display: none;
  }

  .card-panel-icon-wrapper {
    float: none !important;
    width: 100%;
    height: 100%;
    margin: 0 !important;
  }
  .card-panel-icon-wrapper .svg-icon {
    display: block;
    margin: 14px auto !important;
    float: none !important;
  }
}
</style>