/**
 * 
 * @param {any} value 
 * @returns {Boolean} truefalse
 */
export const isString = function (value) {
  return typeof value === 'string'
}
/**
 * JavaScripttruefalse
 * @param {Object} value 
 * @returns {Boolean}
 */
export const isArray = function (value) {
  if ('isArray' in Array) {
    return Array.isArray(value)
  }
  return toString.call(value) === '[object Array]'
}
/**
 * 
 * @param {any} value 
 * @param {Boolean} allowEmptyString 
 * @returns {Boolean} truefalse
 */
export const isEmpty = function (value, allowEmptyString) {
  return value === undefined || value === null || (!allowEmptyString ? value === '' : false) || isArray(value) && value.length === 0
}

/**
 * 
 * @param {any} value 
 * @returns {Boolean} truefalse
 */
export const isObject = function (value) {
  // eslint-disable-next-line no-useless-call
  if (toString.call(null) === '[object Object]') {
    // ownerDocumentDOM
    return value !== null && toString.call(value) === '[object Object]' && value.ownerDocument === undefined
  }

  return toString.call(value) === '[object Object]'

}

/**
 * 
 * @param {any} value 
 * @returns {Boolean} truefalse
 */
export const isSimpleObject = function (value) {
  return value instanceof Object && value.constructor === Object
}
/**
 * 
 * @param {Object} object 
 * @return {Boolean} true
 */
export const isEmptyObject = function (object) {
  var key
  for (key in object) {
    if (object.hasOwnProperty(key)) {
      return false
    }
  }

  return true
}
/**
 * boolean
 * @param {any} value 
 * @returns {Boolean} trueBoolfalseBool
 */
export const isBoolean = function (value) {
  return typeof value === 'boolean'
}

/**
 * true false
 * @param {Object} value 
 * @return {Boolean} true false
 */
export const isNumber = function (value) {
  return typeof value === 'number' && isFinite(value)
}

/**
 * JavaScripttruefalse
 * @param {Object} value .
 * @return {Boolean} JavaScripttruefalse
 */
export const isFunction = function (value) {
  if (typeof document !== 'undefined' && typeof document.getElementsByTagName('body') === 'function') {
    return !!value && toString.call(value) === '[object Function]'
  }

  return !!value && typeof value === 'function'
}
/**
 * 
 */
function s4() {
  return ((1 + Math.random()) * 0x10000 | 0).toString(16).substring(1)
}
export const guid = function () {
  return `${s4() + s4()}-${s4()}-${s4()}-${s4() + s4() + s4()}`
}

export function dateFormat(time, format) {
  var date = new Date(time)
  if (isEmpty(format)) {
    format = 'yyyy-MM-dd'
  }
  var o = {
    'M+': date.getMonth() + 1, // 
    'd+': date.getDate(), // 
    'H+': date.getHours(), // 
    'h+': date.getHours(), // 
    'm+': date.getMinutes(), // 
    's+': date.getSeconds(), // 
    'q+': Math.floor((date.getMonth() + 3) / 3), // 
    'S': date.getMilliseconds() // 
  }
  if (/(y+)/.test(format)) {
    format = format.replace(RegExp.$1, `${date.getFullYear()}`.substr(4 - RegExp.$1.length))
  }
  for (var k in o) {
    if (new RegExp(`(${k})`).test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length === 1 ? o[k] : `00${o[k]}`.substr(`${o[k]}`.length))
    }
  }
  return format
}

/**
 * 
 * @param {*} value 
 * @param {*} isObj 
 * @returns
 */
export function toHex(value, isObj) {
  var result = ''
  var str = value
  if (isObj && isObject(value)) {
    str = JSON.stringify(value)
  }
  if (!isEmpty(str)) {
    str = str.toString()
    for (var i = 0; i < str.length; i++) {
      var temp = str.charCodeAt(i).toString(16)
      if (temp.length === 1) temp = `0${temp}00`
      else if (temp.length === 2) temp = `${temp}00`
      else if (temp.length === 3) temp = `${temp.substring(1, 3)}0${temp.substring(0, 1)}`
      else if (temp.length === 4) temp = temp.substring(2, 4) + temp.substring(0, 2)
      result += temp
    }
  }
  return result.toLowerCase()
}
export function formHex(value, isObj) {
  var result = ''
  if (!isEmpty(value)) {
    value = value.toString()
    for (var i = 0; i < value.length; i += 4) {
      result = result + String.fromCharCode(`0x${value.substring(i + 2, i + 4)}${value.substring(i, i + 2)}`)
    }
  }
  if (!isEmpty(result) && isObj) {
    return JSON.parse(result)
  }
  return result
}


/**
 * 
 * @param {*} path 
 * @returns truefalse
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}
// 
export default {
  isString,
  isEmpty,
  isArray,
  isBoolean,
  isFunction,
  isNumber,
  isObject,
  isSimpleObject,
  isEmptyObject,
  guid,
  dateFormat,
  isExternal,
  toHex,
  formHex
}
